import { Component, OnInit } from '@angular/core';

import { StaffService } from '../../../../api/services/staff.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';

import * as _ from 'underscore';

@Component({
  selector: 'app-staff-shifts',
  templateUrl: './staff-shifts.component.html',
  styleUrls: ['./staff-shifts.component.scss']
})
export class StaffShiftsComponent implements OnInit {
  
  isShiftsLoaded:boolean = true;

  isShiftTableLoaded:boolean = false;

  shiftTypeList:any;

  shift:any;
  shiftId:any;
  shiftTypeId:string = "";
  shiftStartTime:any = "";
  shiftTotalHrs:number = 0;

  shiftsListData:any;
  shiftData:string = "";

  shiftType:string = "";

  isEditShift:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 20;

  constructor(
    private staffService:StaffService,
  	private lookupService:LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getShiftType(id) {
    
    var data = _.filter( this.shiftTypeList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.shiftType = data[0].lookupValueName;
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  submitShiftsForm(form) {

    this.isShiftsLoaded = false;

    if(!this.isEditShift) {

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "shiftTypeId": parseInt(this.shiftTypeId),
        "startTime": moment(this.shiftStartTime).format('HH:mm'),
        "totalHours": this.shiftTotalHrs,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-12-03T06:11:51.972Z",
        "updatedBy": 0,
        "updatedOn": "2019-12-03T06:11:51.972Z"

      }

      this.staffService.addShift(details).subscribe((res:any) => {

        if(res.message) {
          this.isShiftsLoaded = true;
          this.getAllShifts();
        }

      },
      error => {
        
      }
      );

   }
   else {

     let details = {
        "shiftId": this.shiftId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "shiftTypeId": parseInt(this.shiftTypeId),
        "startTime": moment(this.shiftStartTime).format('HH:mm'),
        "totalHours": this.shiftTotalHrs,
        "isActive": true,
        "insertedBy": 0,
        "insertedOn": "2019-12-03T06:11:51.972Z",
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": "2019-12-03T06:11:51.972Z"

      }

      this.staffService.updateShift(details).subscribe((res:any) => {

        if(res.message) {
          this.isShiftsLoaded = true;
          this.getAllShifts();
        }

      },
      error => {
        
      }
      );

   }
    

  }

  editShift(shift){

    this.isEditShift = true;
    this.shiftTypeId = shift.shiftTypeId;
    this.shiftStartTime = moment(shift.startTime, 'HH:mm').toDate();
    this.shiftTotalHrs = shift.totalHours;
    this.shiftId = shift.shiftId;
  }


  getAllShifts(){

    this.isShiftTableLoaded = false;

    this.staffService.getAllShiftsByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.shiftsListData = res.filter(item => {
        return item.isActive;
      });


      this.totalItems = this.shiftsListData.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }

      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isShiftTableLoaded = true;

    },
    error => {
      
    }
    );

  }

  ngOnInit() {

  	this.shift = {};

    this.getAllShifts();

  	//shift type
    this.lookupService.getLookupValueByLookupTypeId(28).subscribe((res:any) => {
          	

       this.shiftTypeList = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );

    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        var params = {
          shiftId: this.shiftsListData[index].shiftId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.isShiftTableLoaded = false;

        this.staffService.deleteShift(params).subscribe((res:any) => {

          this.shiftsListData.splice(index, 1);
          this.totalItems = this.shiftsListData.length;
          this.isShiftTableLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    } )

  }

}
