import { Component, OnInit, Injector, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { StaffService } from '../../../../api/services/staff.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-staff-attendance',
  templateUrl: './staff-attendance.component.html',
  styleUrls: ['./staff-attendance.component.scss']
})
export class StaffAttendanceComponent implements OnInit {
  
  entryListData:any;

  isEntryDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 20;

  entry:any;
  entryData:string = "";

  staffDetails:any;
  gateDataList:any;

  officalStaffsDataList:any;
  personalStaffsDataList:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private staffService:StaffService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService: CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getInGateName(id) {
  	
  	var data = this.gateDataList.filter( item => {
  		return item.gateId == id;
  	});

  	if(data === undefined || data.length === 0) {
  		this.entry.inGateId = "";
  		return ""
  	}
  	else {
  		this.entry.inGateId = data[0].gateName;
  		return data[0].gateName;
  	}

  }

  getOutGateName(id) {
  	
  	var data = this.gateDataList.filter( item => {
  		return item.gateId == id;
  	});

  	if(data === undefined || data.length === 0) {
  		this.entry.outGateId = "";
  		return ""
  	}
  	else {
  		this.entry.outGateId = data[0].gateName;
  		return data[0].gateName;
  	}

  }

  getStaffName(id) {

  	var data = this.staffDetails.filter( item => {
  		return item.staffId == id;
  	});

  	if(data === undefined || data.length === 0) {
  		this.entry.firstName = "";
  		return ""
  	}
  	else {
  		this.entry.firstName = data[0].firstName;
  		return data[0].firstName;
  	}
  	

  }


  getJobTitle(id){

  	var data = this.staffDetails.filter( item => {
  		return item.staffId == id;
  	});

  	if(data === undefined || data.length === 0) {
  		this.entry.jobTitle = "";
  		return ""
  	}
  	else {
  		var selectedStaffCateogryId = data[0].staffCategoryId;
  		var selectedSubStaffCateogryId = data[0].staffSubCategoryId;

  		// offical staff
  			if(selectedStaffCateogryId == 115) {
  				
  				let name = this.officalStaffsDataList.filter(item => {
  					if(item.lookupValueId == selectedSubStaffCateogryId){
  						return item.lookupValueName;
  					}
  				})


  				 if(name === undefined || name.length == 0){
  				 	this.entry.jobTitle = "";
  				 	return ""
  				 }
  				 else {
  				 	this.entry.jobTitle = name[0].lookupValueName;
  				 	return name[0].lookupValueName;
  				 }

  				

  			}
  			else {
  				let name = this.personalStaffsDataList.filter(item => {
  					if(item.lookupValueId == selectedSubStaffCateogryId){
  						return item.lookupValueName;
  					}
  				})
  				if(name === undefined || name.length == 0){
  				 	this.entry.jobTitle = "";
  				 	return ""
  				 }
  				 else {
  				 	this.entry.jobTitle = name[0].lookupValueName;
  				 	return name[0].lookupValueName;
  				 }
  			}



  	}

  }


  ngOnInit() {

  	this.entry = {};


  	this.staffDetails = [];
  	this.gateDataList = [];

  	this.officalStaffsDataList = [];
  	this.personalStaffsDataList = [];


  	this.staffService.getAllAttendancesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

    //filter active true items
    this.entryListData = res.filter(data => {
      return data.isActive
    });

    _.each(this.entryListData, item => {
    	
    	this.staffService.getStaffByStaffId(item.staffId).subscribe((staff:any) => {

    		this.staffDetails.push(staff[0]);

    	});

    });

    //get all gates
	this.staffService.getAllGatesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

		this.gateDataList = res;


	});

	//get all offical staff 
    this.lookupService.getLookupValueByLookupTypeId(26).subscribe((res:any) => {
       
 		this.officalStaffsDataList = res.filter(item => {
 			return item.isActive;
 		});

    },
    error => {
      
    }
    );

    //get all personal staffs 
    this.lookupService.getLookupValueByLookupTypeId(27).subscribe((res:any) => {
       
 		this.personalStaffsDataList = res.filter(item => {
 			return item.isActive;
 		});


    },
    error => {
      
    }
    );


    this.totalItems = this.entryListData.length;

    if(this.totalItems>this.itemLimit){
      this.ItemEndIndex = this.itemLimit;
    }

    else {
      this.ItemEndIndex = this.totalItems;
    }

    this.isEntryDataLoaded = true;

  });






  }

}
