import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { StaffService } from '../../../../api/services/staff.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-staff-gates',
  templateUrl: './staff-gates.component.html',
  styleUrls: ['./staff-gates.component.scss']
})
export class StaffGatesComponent implements OnInit {
  
  gate:any;

  isGateLoaded:boolean = true;

  isGateTableLoaded:boolean = false;

  modalService:any;

  gateListData:any;

  constructor(
  	private injector: Injector,
  	public dialog: MatDialog,
    private staffService:StaffService,
    private lookupService:LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
  	this.modalService = this.injector.get(ModalService);
  }

  submitGateForm(form) {

  	this.isGateLoaded = false;

  	var details = {

	  "apartmentId": parseInt(this.cookieService.get('apartmentId')),
	  "gateName": this.gate.gateName,
	  "comments": this.gate.comments,
	  "isActive": true,
	  "insertedBy": parseInt(this.cookieService.get('userId')),
	  "insertedOn": "2019-12-02T16:55:38.372Z",
	  "updatedBy": 0,
	  "updatedOn": "2019-12-02T16:55:38.372Z"

	}

	this.staffService.addGate(details).subscribe((res:any) => {

		if(res.message) {

			this.isGateLoaded = true;
			this.getAllGates();
		}

	}, error => {

	})

  }

  getAllGates(){

  	this.staffService.getAllGatesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

  		this.isGateTableLoaded = true;
  		this.gateListData = res.filter(item => {
  			return item.isActive;
  		});


  	}, error => {

	});

  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  ngOnInit() {

  	this.gate = {};

  	this.getAllGates();

  	// delete gate
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        var params = {
          gateId: this.gateListData[index].gateId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.isGateTableLoaded = false;

        this.staffService.deleteGate(params).subscribe((res:any) => {

          this.gateListData.splice(index, 1);
          this.isGateTableLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    });


  }

}
