import { Component, OnInit, Injector, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { StaffService } from '../../../../api/services/staff.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

import * as moment from 'moment';

@Component({
  selector: 'app-staff-maintain-staff',
  templateUrl: './staff-maintain-staff.component.html',
  styleUrls: ['./staff-maintain-staff.component.scss']
})
export class StaffMaintainStaffComponent implements OnInit {

  staffListData:any;

  isStaffDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 20;

  staff:any;
  staffData:string = "";

  modalService:any;

  totalFilterItems:any;
  visitTypeData:any;

  staffTypeData:any;
  staffOfficalCategoryData:any;
  staffPersonalCategoryData:any;
  staffTypeId:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private injector: Injector,
    private staffService:StaffService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService: CookieService
  ) {
    this.modalService = this.injector.get(ModalService);
   }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  getTimeFormat(dateTime){
    return moment(dateTime).format("YYYY-MM-DD HH:mm");
  }

  getStaffCategory(id){
     
    var data = _.filter( this.staffTypeData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.staffTypeId = data[0].lookupValueId;
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getStaffSubCategory(id) {
    if(this.staffTypeId == 115){

      var data = _.filter( this.staffOfficalCategoryData, function( item ) {
      if(item.lookupValueId === id)
        return item;
      } );
      if(data === undefined || data.length == 0){
        return '';
      }
      else {
        return data[0].lookupValueName;
      }

    }
    else {

      var data = _.filter( this.staffPersonalCategoryData, function( item ) {
      if(item.lookupValueId === id)
        return item;
      } );

      if(data === undefined || data.length == 0){
        return '';
      }
      else {
        return data[0].lookupValueName;
      }

    }
    

  }

  ngOnInit() {

    this.staffService.getAllStaffsByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

    //filter active true items
    this.staffListData = res.filter(data => {
      return data.isActive
    });


    this.totalItems = this.staffListData.length;

    if(this.totalItems>this.itemLimit){
      this.ItemEndIndex = this.itemLimit;
    }

    else {
      this.ItemEndIndex = this.totalItems;
    }

    this.isStaffDataLoaded = true;

  });

    //staff type
    this.lookupService.getLookupValueByLookupTypeId(25).subscribe((res:any) => {
          
       this.staffTypeData = res.filter( item => {
          return item.isActive;
       });

    },
    error => {
      
    }
    );

    //offical category type
    this.lookupService.getLookupValueByLookupTypeId(26).subscribe((res:any) => {
          
       this.staffOfficalCategoryData = res.filter( item => {
          return item.isActive;
       });

       console.log(this.staffOfficalCategoryData);

    },
    error => {
      
    }
    );

    //personal category type
    this.lookupService.getLookupValueByLookupTypeId(27).subscribe((res:any) => {
          
       this.staffPersonalCategoryData = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );


    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        var params = {
          staffId: this.staffListData[index].staffId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.isStaffDataLoaded = false;

        this.staffService.deleteStaff(params).subscribe((res:any) => {

          this.staffListData.splice(index, 1);
          this.totalItems = this.staffListData.length;
          this.isStaffDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    } )


  }

}
